<template>
  <div class="module-type3">
    <section>
      <div
        class="module-item"
        v-for="item in moduleObj.albumList"
        :key="item.programId"
        @click="routerPage(item.programId)"
      >
        <div class="pic">
          <img :src="item.programImg" />
        </div>
        <h3>{{ item.programTit }}</h3>
        <p>
          <img src="../assets/imgs/images/playIcon.png" />
          <span>{{ item.programNum }}人阅读</span>
        </p>
      </div>
    </section>
  </div>
</template>

<script>
export default {
  name: "ModuleType3",
  props: {
    moduleObj: Object,
  },
  methods: {
    routerPage(id) {
      this.$router.push({
        name: "section",
        params: {
          type: 3,
          id: id,
        },
      });
    },
  },
};
</script>

<style lang="scss" scoped>
@font-face {
